@import "../../scss/variables";

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 2rem;
   .cards {
      width: 500px;
      height: 350px;
      position: relative;
      overflow: hidden;
      &:hover .card-over{
        transform: translateX(0%);
      }
        .title {
            position: absolute;
            top: 0%;
            left: 0%;
            background-color: rgba(0, 0, 0, 0.8);
            color: $cardNavBgColor;
            padding: 1rem;
            border-radius: 10px 20px 20px 20px;
        }
        .date {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-color: rgba(0, 0, 0, 0.8);
            color: $cardNavBgColor;
            font-size: 0.3rem;
            padding: 1rem;
            border-radius: 10px;
        }
        img {
            width: 500px;
            height: 350px;
            object-fit: cover;
            border-radius: 10px;
                }
        .card-over {
            position: absolute;
            z-index: 3;
            bottom: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.8);
            width: 100%;
            padding: 1rem;
            font-size: 1.5rem;
            border-radius: 10px;
            transform: translateX(100%);
            transition: transform 0.7s ease-in-out;
            max-height: 75%;
            overflow: auto;
            text-align: justify;

            &::-webkit-scrollbar {
                display: none;
            }
}

}
}